import Image from "next/image"

interface InfrastructureCard {
  title: string
  description: string
  image: string
  accent: string
}

const createCardImage = (start: string, end: string, accent: string) =>
  `data:image/svg+xml;utf8,${encodeURIComponent(`
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 900">
      <defs>
        <linearGradient id="g" x1="0%" y1="0%" x2="100%" y2="100%">
          <stop offset="0%" stop-color="${start}" />
          <stop offset="100%" stop-color="${end}" />
        </linearGradient>
      </defs>
      <rect width="1200" height="900" fill="url(#g)" rx="72" />
      <circle cx="1020" cy="220" r="160" fill="${accent}" fill-opacity="0.22" />
      <circle cx="240" cy="720" r="220" fill="${accent}" fill-opacity="0.16" />
      <path d="M180 260 Q340 120 520 220 T900 280" stroke="${accent}" stroke-width="36" stroke-linecap="round" stroke-opacity="0.25" fill="none" />
    </svg>
  `)}`

const infrastructureCards: InfrastructureCard[] = [
  {
    title: "3D Printing",
    description: "Rapid prototyping bays with calibrated FDM and resin printers ready for overnight iterations.",
    image: createCardImage("#f75700", "#feb47b", "#002449"),
    accent: "#f75700",
  },
  {
    title: "Laser Cutting",
    description: "High-precision laser tables for acrylic, plywood, and composites with on-site safety supervision.",
    image: createCardImage("#4facfe", "#00f2fe", "#002449"),
    accent: "#0284c7",
  },
  {
    title: "Metal Cutting",
    description: "CNC milling, bending presses, and finishing tools configured for fast track structural work.",
    image: createCardImage("#43cea2", "#185a9d", "#002449"),
    accent: "#0b5f4b",
  },
  {
    title: "Others",
    description: "Electronics benches, testing rigs, and mentor pods for cross-discipline troubleshooting.",
    image: createCardImage("#8360c3", "#2ebf91", "#002449"),
    accent: "#5b21b6",
  },
]

export function InfrastructureScrollStack() {
  return (
    <section className="w-full border-t border-[#d9e2f2] bg-white px-6 py-12">
      <div className="mx-auto flex w-full max-w-5xl flex-col gap-16">
        <header className="text-center">
          <p className="text-xs font-semibold uppercase tracking-[0.4em] text-[#7b859c]">Infrastructure at GITAM</p>
          <h2 className="mt-2 text-3xl font-bold text-[#002449]">Scroll Through the Prototyping Stack</h2>
          <p className="mt-3 text-sm text-[#3f4a63]">
            Each bay stays online round the clock with dedicated coordinators, so teams can move from sketch to hardware without
            losing momentum.
          </p>
        </header>
        <div className="relative">
          <div className="flex flex-col">
            {infrastructureCards.map((card, index) => (
              <article key={card.title} className="relative min-h-[90vh] sm:min-h-[110vh]">
                <div className="sticky top-24 sm:top-28">
                  <div className="group grid gap-8 rounded-[32px] border border-[#cbd6eb] bg-white/95 p-6 shadow-[0_20px_48px_rgba(0,36,73,0.12)] backdrop-blur-sm transition-transform duration-500 hover:-translate-y-1 sm:grid-cols-[minmax(0,320px)_1fr] sm:p-8">
                    <div className="overflow-hidden rounded-3xl border border-[#dfe6f5] bg-[#f6f8fd]">
                      <Image
                        src={card.image}
                        alt={card.title}
                        width={960}
                        height={720}
                        className="h-full w-full object-cover transition-transform duration-700 ease-out group-hover:scale-105"
                        priority={index === 0}
                      />
                    </div>
                    <div className="flex flex-col justify-center gap-4 text-left">
                      <span className="text-[11px] font-semibold uppercase tracking-[0.35em] text-[#7b859c]">Fabrication Suite</span>
                      <h3 className="text-2xl font-semibold text-[#002449]">{card.title}</h3>
                      <p className="text-sm text-[#4c5874]">{card.description}</p>
                      <span className="h-px w-16" style={{ backgroundColor: card.accent }} />
                    </div>
                  </div>
                </div>
              </article>
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}
